import { connect } from 'react-redux'

import {
  clearFilters,
  performBatchAction,
  setFilterValue,
  setSearchTerm,
  toggleSidebar,
} from '../redux/modules/search/actions'

import {
  anyResultsSelected,
  getToolbarFilters,
} from '../redux/modules/search/selectors'

import ToolBar from '../components/ToolBar'

const mapStateToProps = (state) => ({
  anyResultsSelected: anyResultsSelected(state),
  batchActions: state.search.batchActions,
  filters: state.search.filters,
  hasSidebar: state.search.sidebarFilters && state.search.sidebarFilters.length > 0,
  searchTerm: state.search.searchTerm,
  toolbarFilters: getToolbarFilters(state),
})

const actionCreators = {
  clearFilters,
  performBatchAction,
  setFilterValue,
  setSearchTerm,
  toggleSidebar,
}

export default connect(mapStateToProps, actionCreators)(ToolBar)



